<template>
  <view class="title-switch-wrapper">
    <!-- 左侧标题和图片 -->
    <view class="left-content">
      <image :src="imageUrl" :alt="title" class="title-icon" mode="aspectFit" />
      <text class="title-text">{{ title }}</text>
    </view>
    <!-- 右侧开关组件 -->
    <switchButton :checked="modelValue" @change="handleChange" color="#2979ff" />
  </view>
</template>

<script>
import switchButton from './switchButton.vue'
export default {
  name: 'activityListHeder',
  components: {
    switchButton,
  },
  props: {
    title: {
      type: String,
      default: '',
    },
    imageUrl: {
      type: String,
      default: 'https://cdn-static.papa.com.cn/social/activity-style.png',
    },
    modelValue: {
      type: Boolean,
      default: false,
    },
  },

  emits: ['update:modelValue'],

  methods: {
    handleChange(e) {
      this.$emit('update:modelValue', e)
      this.$emit('change', e)
    },
  },
}
</script>

